////
/// Header Module : navigation
/// @group header
////

#{ns(header)} {
  #{ns(nav)} {
    @include respond-from(lg) {
      justify-content: flex-start;

      &__list {
        max-width: calc(100% + #{space(4v)});
      }

      #{ns(menu)}::after,
      #{ns(mega-menu)} {
        @include shadow(md);
      }
    }
  }
}
